<template>
<div style="width: 1000px;margin: auto;">
  <!-- 查询域 -->
<el-row type="flex" justify="center" style="margin-top:50px;margin-bottom:40px"  >
          <el-form :inline="true" >
            <el-form-item>
              <el-autocomplete
                class="inline-input"
                v-model="queryParams.keyWord"
                @input="handleDebounce" @keyup.enter.native="query(queryParams.keyWord)"
                :fetch-suggestions="showHistory"
                placeholder="...关键词..."
                prefix-icon="el-icon-search"
                @select="handleSearchSelect"
                style="width:400px"
              ></el-autocomplete>
            </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="query(queryParams.keyWord)">搜索</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="text"  size="mini" icon="el-icon-setting" circle @click="showConfig"></el-button>
          </el-form-item>
        </el-form>
  </el-row>
  
  <el-row type="flex" justify="end" style="margin-bottom:0px" >
      
       <el-col :span="11">
         <el-radio-group v-model="filterTypeRadio" @change="query(queryParams.keyWord)" style="margin-right:20px" size="mini">
          <el-radio-button label="ALL">所有</el-radio-button>
          <el-radio-button label="视频">视频</el-radio-button>
          <el-radio-button label="音频">音频</el-radio-button>
          <el-radio-button label="图片">图片</el-radio-button>
          <el-radio-button label="文档">文档</el-radio-button>
          <el-radio-button label="压缩包">压缩包</el-radio-button>
          <el-radio-button label="可执行">可执行</el-radio-button>
        </el-radio-group>
       </el-col>
  </el-row>
      <!-- 表格 -->
      <el-table :data="pager.beans" @row-click="selectRow" @row-dblclick = "dblOpen" style="font-size:12px"
       stripe highlight-current-row v-loading="$store.state.loading" :default-sort = "{prop: 'fileModifyTime', order: 'descending'}" >
        <el-table-column v-if="showSequence" type="index"  width="50" label="序列" align="center">
        </el-table-column>
        <el-table-column prop="fileName" label="名称" min-width="200" sortable align="left">
           <template slot-scope="scope">
            <el-tooltip class="item" effect="light" :content=scope.row.fileName placement="bottom-start" :open-delay=openDelay >
                <el-tag type="info" effect="plain" size="medium">
                  <template v-if="scope.row.fileType=='文件夹'"><i class="el-icon-folder-opened" style="color:#87cefa"></i></template>
                  <template v-if="scope.row.fileType=='视频'"><i class="el-icon-video-camera" style="color:blue"></i></template>
                  <template v-if="scope.row.fileType=='音频'"><i class="el-icon-headset" style="color:blue"></i></template>
                  <template v-if="scope.row.fileType=='图片'"><i class="el-icon-picture-outline" style="color:blue"></i></template>
                  <template v-if="scope.row.fileType=='文档'"><i class="el-icon-document" style="color:grey"></i></template>
                  <template v-if="scope.row.fileType=='压缩包'"><i class="el-icon-collection" style="color:black"></i></template>
                  <template v-if="scope.row.fileType=='文本'"><i class="el-icon-reading" style="color:grey"></i></template>
                  <template v-if="scope.row.fileType=='可执行'"><i class="el-icon-position" style="color:lightgreen"></i></template>
                  <template v-if="scope.row.fileType=='-'"><i class="el-icon-info" style="color:lightgrey"></i></template>
                  <template v-if="scope.row.fileType=='其它'"><i class="el-icon-warning-outline" style="color:lightgrey"></i></template>
                  {{scope.row.fileName}}
                  </el-tag>
              </el-tooltip>
           </template>
        </el-table-column>
        <el-table-column prop="path" label="路径" sortable align="left">
          <template slot-scope="scope">
              <el-tooltip class="item" effect="light" :content=scope.row.path placement="bottom-start" :open-delay=openDelay >
                <span style="display: inline-block;white-space: nowrap;word-break: keep-all;">
             {{scope.row.path}}
              </span>
              </el-tooltip>
           </template>
        </el-table-column>
        <el-table-column prop="fileType" label="类型" sortable width="80" align="center">
          <template slot-scope="scope">
            <span style="display: inline-block;white-space: nowrap;word-break: keep-all;">
                        {{scope.row.fileType}}
            </span>
           </template>
        </el-table-column>
        <el-table-column prop="fileSize" label="大小" sortable width="90" align="center">
          <template slot-scope="scope">
            <span style="display: inline-block;white-space: nowrap;word-break: keep-all;">
                        {{scope.row.fileSize==0?"-":convert(scope.row.fileSize)}}
            </span>
           </template>
        </el-table-column>
        <el-table-column prop="fileModifyTime" label="修改时间" sortable width="180" align="center">
          <template slot-scope="scope">
           <span style="display: inline-block;white-space: nowrap;word-break: keep-all;">
             {{scope.row.fileModifyTime}}
              </span>
          </template>
        </el-table-column>
        <el-table-column  label="" width="50" align="center">
          <template slot-scope="scope">
            <el-dropdown @command="handleCommand" trigger="click">
              <span class="el-dropdown-link">
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item icon="el-icon-folder-opened" v-if="scope.row.remote==false" command="open">打开</el-dropdown-item>
                <el-dropdown-item icon="el-icon-folder" v-if="scope.row.remote==false" command="openDIR">所在文件夹</el-dropdown-item>
                <el-dropdown-item icon="el-icon-download" command="downloadFile">下载</el-dropdown-item>
                <el-dropdown-item icon="el-icon-share" command="shareFile">分享</el-dropdown-item>
                <el-dropdown-item icon="el-icon-key" command="shareFile1">提取码分享</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
           </template>
        </el-table-column>
      </el-table>
      <el-tag v-if="showMoreVisible">
        按 ‘回车’ 加载更多...
      </el-tag>
      <!-- 设置对话框 -->
      <el-dialog title="设置"  width="600px"  :visible.sync="configVisible" >
        <!-- 添加索引对话框 -->
          <el-dialog title="添加索引目录"  width="400px"  :visible.sync="addIndexVisible" append-to-body >
            <el-form>
              <el-form-item label="路径">
                <el-col :span="20">
                    <el-input size="mini" v-model="addIndexParams.path" placeholder="D:\\xxx , ftp://xxx , smb://xxx">
                    </el-input>
                </el-col>
                </el-form-item>
              <el-form-item label="类型">
                    <el-select size="mini" v-model="addIndexParams.type" style="width:75px" placeholder="">
                      <el-option label="本地" value="local"></el-option>
                      <el-option label="远程" value="remote"></el-option>
                    </el-select>
                </el-form-item>
                
                <el-form-item v-if="addIndexParams.type=='remote'" label="认证信息">
                  <el-col :span="7">
                     <el-input size="mini" v-model="addIndexParams.userName" placeholder="用户名">
                    </el-input>
                </el-col>
                   <el-col :span="7">
                    <el-input size="mini"  v-model="addIndexParams.passwd" type="password" placeholder="密码">
                    </el-input>
                    </el-col>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" size="mini" @click="createIndex(addIndexParams)">提交</el-button>
                  <el-button @click="resetAddIndexForm" size="mini">重置</el-button>
                </el-form-item>
            </el-form>
          </el-dialog>
         <el-tabs tab-position="left" v-model="activeTab" style="height: 400px;" @tab-click="indexLoad" >
           <!-- 搜索设置 -->
          <el-tab-pane label="搜索设置" name="searchConfigTab">
             <el-form >
                <el-form-item label="结果数">
                    <el-select v-model="queryParams.top" style="width:75px;margin-right:10px" @change="refreshPage" placeholder="">
                      <el-option label="10" value="10"></el-option>
                      <el-option label="20" value="20"></el-option>
                      <el-option label="50" value="50"></el-option>
                      <el-option label="100" value="100"></el-option>
                    </el-select>
                    <el-switch
                      v-model="showSequence"
                      inactive-text="显示行数">
                    </el-switch>
                </el-form-item>
                <el-form-item label="搜索类型">
                     <el-radio-group v-model="queryParams.type">
                      <el-radio label="startwith">startwith</el-radio>
                      <el-radio label="contains">contains</el-radio>
                      <el-radio label="endwith">endwith</el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
          </el-tab-pane>
          <!-- 索引目录 -->
          <el-tab-pane label="索引目录" name="indexDirectoryTab" >
            <el-table
                :data="indexPager.beans"
                height="400px"
                :default-sort = "{prop: 'path', order: 'ascending'}"
                style="width: 100%;font-size:5px">
                <el-table-column  min-width="80"
                  label="路径" sortable  align="left"
                  prop="path">
                </el-table-column>
                <el-table-column
                  label="类型"  width="60"  align="center"
                  prop="isRemote">
                   <template slot-scope="scope">
                    <el-tag size="mini">{{scope.row.isRemote?"远程":"本地"}}</el-tag>
                </template>
                </el-table-column>
                <el-table-column
                  label="文件数"  width="65"  align="center"
                  prop="fileCount">
                </el-table-column>
                <el-table-column  width="90"  align="center"
                  label="索引时间"
                  prop="indexTime">
                </el-table-column>
                <el-table-column align="center" width="80">
                  <template slot="header" slot-scope="scope">
                     <el-button
                     type="text"
                      icon="el-icon-plus"
                      @click="showAddIndex"></el-button>
                  </template>
                  <template slot-scope="scope">
                    <el-button type="text" @click="handleReIndex(scope.row)" :loading="scope.row.loading" icon="el-icon-refresh-right"></el-button>
                  </template>
                </el-table-column>
              </el-table>
          </el-tab-pane>
          <!-- 修改密码 -->
          <el-tab-pane label="修改密码" name="updateUserTab">
             <el-form >
                <el-form-item label="旧密码">
                  <el-col :span="16">
                    <el-input size="mini" type="password" v-model="updateUserParams.oldPass">
                    </el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="新密码">
                  <el-col :span="16">
                    <el-input size="mini" type="password" v-model="updateUserParams.newPass">
                    </el-input>
                    </el-col>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" size="mini" @click="updateUser(updateUserParams)">修改</el-button>
                  <el-button @click="resetUpdateUserForm" size="mini">重置</el-button>
                </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </el-dialog>
    </div>
</template>
<style>
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.dialog-footer .step{
  color: #000;
}
.dialog-footer .stepBtn{
  color: #000;
}
</style>
<script>
function Debounce(fn){
      let delay = 500;
      let timer;
      return function () {
        let args = arguments;
        if (timer) {
          clearTimeout(timer);
        }
        timer = setTimeout(() => {
          timer = null;
          fn.apply(this, args);
        }, delay);
      };
    }
export default {
  name: "index",
  data() {
    return {
      filterTypeRadio:"ALL",
      searchHistory:[],
      activeTab:"searchConfigTab",
      configVisible:false,
      showMoreVisible:false,
      showSequence:true,
      addIndexVisible:false,
      queryParams:{"top":20,"type":"startwith"},
      addIndexParams:{"type":"local"},
      updateUserParams:{},
      currentRow:{},
      openDelay:1000,
      pager: { beans: [] },
      indexPager: { beans: [] }
    };
  },
  mounted() {
    this.initSearchHistory();
    this.queryRecent();
  },
  methods: {
    updateUser(params){
      if(!params.oldPass)return;
      if(!params.newPass)return;
      this.$http.post("/admin/updatePassword",
        'oldPass='+params.oldPass+"&newPass="+params.newPass
      ).then(res => {
        this.$message({
            message: res.data.msg,
            type: res.data.code==-1?"error":"success"
        });
        this.resetUpdateUserForm();
      });
    },
    copyText(text){
      var tag = document.createElement('input');
      tag.setAttribute('id', 'cp_hgz_input');
      tag.value = text;
      document.getElementsByTagName('body')[0].appendChild(tag);
      document.getElementById('cp_hgz_input').select();
      document.execCommand('copy');
      document.getElementById('cp_hgz_input').remove();
       this.$message({
          message: '分享链接已复制',
          type: 'success'
        });
    },
    handleSearchSelect(item){
      this.queryParams.keyWord = item.value;
    },
    showHistory(queryStr,cb){
      var history = [];
      for(var i =0;i<this.searchHistory.length;i++){
        history.push({"value":this.searchHistory[i]});
      }
      var r = queryStr ? history.filter(this.createFilter(queryStr)) : history;
      cb(r);
    },
    putSearchHistory(key){
      if(!key) return;
      if(!this.searchHistory.includes(key)){
        this.searchHistory.unshift(key);
      }else{
        let i = this.searchHistory.indexOf(key);
        this.searchHistory.splice(i,1);
        this.searchHistory.unshift(key);
      }
      while(this.searchHistory.length>5){
        this.searchHistory.pop();
      }
       localStorage.setItem("historyList",JSON.stringify(this.searchHistory));
    },
    createFilter(queryString) {
        return (history) => {
          return (history.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    },
    initSearchHistory(){
      if(JSON.parse(localStorage.getItem("historyList"))){
        this.searchHistory = JSON.parse(localStorage.getItem("historyList"));
      }
    },
    handleDebounce:Debounce(function(){
      this.querySearchAsync(this.queryParams.keyWord)
      }),
    handleReIndex(row){
      row.loading = true;
      this.reIndex(row);
    },
    indexLoad(tab,event){
      if(tab.name=='indexDirectoryTab'){
        this.queryIndex();
      }
    },
    showConfig(){
      this.configVisible=true;
    },
    showAddIndex(){
      this.addIndexVisible=true;
    },
    selectRow(row){
      this.currentRow = row;
    },
    dblOpen(row){
      this.openFile(row.path+(row.path.endsWith('\\')?'':'\\')+row.fileName);
    },
    querySearchAsync(key) {
      this.showMoreVisible = false;
      this.$http.get("/file/quickSearch",{
        params: {
          key:this.queryParams.keyWord==""?undefined:this.queryParams.keyWord,
          type:this.queryParams.type==""?undefined:this.queryParams.type,
          fileType:this.filterTypeRadio==""?undefined:this.filterTypeRadio,
          top:7
        }
      }).then(res => {
        if(res.data.beans.length==7) this.showMoreVisible=true;
        this.pager = res.data;
      });
    },
    openFile(path) {
      this.$http.get("/file/openFile",{
        params: {
          path:path==""?undefined:path
        }
      }).then(res => {
        
      });
    },
    openParentFile(path) {
      this.$http.get("/file/openDIR",{
        params: {
          path:path==""?undefined:path
        }
      }).then(res => {
        
      });
    },
    queryRecent(){
      this.$http.get("/file/recent",{
        params: {
          top:this.queryParams.top==""?undefined:this.queryParams.top,
          fileType:this.filterTypeRadio==""?undefined:this.filterTypeRadio
        }
      }).then(res => {
        this.pager = res.data;
      });
    },
    query(key) {
      if(!key){
        document.title = 'Search Everything';
        this.queryRecent();
        return;
      }
      this.showMoreVisible = false;
      this.putSearchHistory(key);
      document.title = key+'_Search';
      this.$http.get("/file/search",{
        params: {
          top:this.queryParams.top==""?undefined:this.queryParams.top,
          key:this.queryParams.keyWord==""?undefined:this.queryParams.keyWord,
          fileType:this.filterTypeRadio==""?undefined:this.filterTypeRadio,
          type:this.queryParams.type==""?undefined:this.queryParams.type
        }
      }).then(res => {
        this.pager = res.data;
      });
    },
    queryIndex() {
      this.$http.get("/index/getList",{
        params: {
          
        }
      }).then(res => {
        this.indexPager = res.data;
      });
    },
    reIndex(params){
      this.$http.get("/index/reIndex",{
        params: {
          id:params.id
        }
      }).then(res => {
        if(res.data.code==0){
          this.queryIndex();
        }
      });
    },
    createIndex(params) {
      params = {"path":this.addIndexParams.path,"type":this.addIndexParams.type=='remote',"loading":true}
      this.addIndexVisible=false;
      this.indexPager.beans.push(params)
      this.$http.get("/index/create",{
        params: {
          path:params.path==""?undefined:params.path,
          type:params.type==""?undefined:this.addIndexParams.type,
          userName:this.addIndexParams.userName==""?undefined:this.addIndexParams.userName,
          passwd:this.addIndexParams.passwd==""?undefined:this.addIndexParams.passwd,
        }
      }).then(res => {
        if(res.data.code==0){
          this.queryIndex();
          this.resetAddIndexForm();
        }
      });
    },
    resetAddIndexForm(){
      this.addIndexParams={"type":"local"};
    },
    resetUpdateUserForm(){
      this.updateUserParams.oldPass="";
      this.updateUserParams.newPass="";
    },
    handleCommand(commnad){
      if(commnad == "open") this.openFile(this.currentRow.path+(this.currentRow.path.endsWith('\\')?'':'\\')+this.currentRow.fileName)
      if(commnad == "openDIR") this.openParentFile(this.currentRow.path+(this.currentRow.path.endsWith('\\')?'':'\\')+this.currentRow.fileName)
      if(commnad == "downloadFile") window.open("/file/downloadFile?ticket="+this.currentRow.id);
      if(commnad == "shareFile") this.shareFile(this.currentRow,false);
      if(commnad == "shareFile1") this.shareFile(this.currentRow,true);
    },
    shareFile(row,accessControl){
      this.$http.get("/share/shareFile",{
        params: {
          fileId:row.id==""?undefined:row.id,
          accessControl:accessControl
        }
      }).then(res => {
        if(res.data.code==0){
          var content = location.host+"/file/download?ticket="+res.data.bean.code;
          if(accessControl){
            content = "分享链接："+content+"  提取码："+res.data.bean.fetchCode;
          }
          this.copyText(content)
        }else{
          this.$message({
            message: res.data.msg,
            type: 'error'
          });
        }
      });
    },
    handleSizeChange(val) {
      this.query(val);
    },
    convert(limit){  
            var size = "";  
            if( limit < 1 * 1024 ){ //如果小于0.1KB转化成B  
                size = limit.toFixed(2) + "B";    
            }else if(limit < 1 * 1024 * 1024 ){//如果小于0.1MB转化成KB  
                size = (limit / 1024).toFixed(2) + "KB";              
            }else if(limit < 1 * 1024 * 1024 * 1024){ //如果小于0.1GB转化成MB  
                size = (limit / (1024 * 1024)).toFixed(2) + "MB";  
            }else{ //其他转化成GB  
                size = (limit / (1024 * 1024 * 1024)).toFixed(2) + "GB";  
            }  
            var sizestr = size + "";   
            var len = sizestr.indexOf("\.");  
            var dec = sizestr.substr(len + 1, 2);  
            if(dec == "00"){//当小数点后为00时 去掉小数部分  
                return sizestr.substring(0,len) + sizestr.substr(len + 3,2);  
            }  
            return sizestr;  
        } ,
    refreshPage(){
      this.query(this.queryParams.keyWord)
    }
  }
};
</script>
